import React, {Component} from 'react';
import './style.css';
import image from '../assets/images/traduceri_resized.jpg';

export default class UnderConstruction extends Component {
  render() {

    return (
        <div className="main">
          <div className="image-container">
            <img src={image} className="image-background"/>
          </div>

          <div className="content">
            <h1>Ajunge in curand</h1>
            <div className="row">
              <div className="col-12"><h4>Traduceri Autorizate</h4></div>
              <div className="col-12"><h2>Portugheza - Romana</h2></div>
              <div className="col-12"><h4>Interpretare</h4></div>
            </div>

            <div className="row">
              <div className="col-12">
                <div className="fa fa-envelope"/>
                <span>MockMail@gmail.com</span>
              </div>
            </div>
          </div>
        </div>
    );
  }
}



